@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&family=Poppins&display=swap);
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px 0;
    height: 30px;
    margin: 10px 20px;
}  
.list {
    display: flex;
    padding: 0;
    
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-size: large;
} 
li {
    margin-left: 15px;
    list-style: none;
}
li:hover {
    cursor: pointer;
}
.resume {
    background-color: aqua;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
} 

.nav-left {
    font-family:  cursive;
    font-size: large;
}
.intro {
    display: flex;
    height: 100vh;
}
.intro-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.intro-right {
    flex: 1 1;
}
.left-wrapper {
    padding: 70px;
    height: 50%;
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}
.left-i {
    font-size: 35px;
    font-weight: 300;
    margin: 0;
    margin-bottom: 10px;
}
.left-name {
    font-size: 65px;
    margin: 0 0 10px 0;
}
.left-title {
    height: 50px;
    overflow: hidden;
}
.title-item {
    height: 50px;
    font-size: 35px;
    font-weight: bold;
    color: #145858;
    display: flex;
    align-items: center;
}
.title-wrapper {
    height: 100%;
    -webkit-animation: anime 6s ease infinite alternate;
            animation: anime 6s ease infinite alternate;
}
.desc {
    font-size: 20px;
    /* width: 80%; */
}
@-webkit-keyframes anime {
    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    100% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    /* 75% {
        transform: translateY(-150px);
    }
    100% {
        transform: translateY(-200px);
    } */
}
@keyframes anime {
    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    100% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    /* 75% {
        transform: translateY(-150px);
    }
    100% {
        transform: translateY(-200px);
    } */
}

.new-head {
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    justify-content: flex-start;
    font-size: 35px;
    font-weight: 700;
    color: #145858;
}

/* left part */

.i-bg {
    -webkit-clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #113946;
    width: 100%;
    height: 100%;
}
.contact {
    height: 100vh;
    position: relative;
}
.c-bg {
    background-color: #59b256;
    height: 100%;
    width: 30px;
    position: absolute;
    z-index: -1;
}
.contact-wrapper {
    display: flex;
}
.contact-left {
    flex: 1 1;
    margin: 50px;
    padding: 40px 0px 0px 40px;
    /* height: 50%; */
}
.desc {
    margin-top: 30px;
}
.contact-right {
    flex: 1 1;
    margin-top: 90px;
}
.title {
    font-size: 60px;
    padding-bottom: 10px;
    margin-top: 30px;
    width: 60%;
}

.info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 400;
    font-size: 25px;
}

form {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
}

input {
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0;
    font-size: 15px;
    padding-left: 10px;
}

textarea {
    width: 90%;
    margin: 10px 0;
    font-size: 15px;
    padding-left: 10px;
}

.button {
    padding: 15px;
    background-color: #59b256;
    color: white;
    border: none;
    cursor: pointer;
    font-weight: 600;
    width: 80px;
    cursor: pointer;
}

.message {
    width: 70%;
}

.c-icons {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

a {
    text-decoration: none;
    
}


.container {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    font-size: 28px;
    /* height: 200px; */
    width: 50px;
    position: fixed;
    /* position: absolute; */
    top: 40%;
    right: 0;
}

.s-icons {
    padding: 9px;
    background-color: aqua;
    cursor: pointer;
}

a {
    text-decoration: none !important;
    color: inherit;
}

a:hover {
    font-size: 30px;
}
* {
    margin: 0;
    padding: 0;
}

.load-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
    background: linear-gradient(to right, rgb(60, 60, 60), black);
}

.load-name {
    font-style: italic;
    color: white;
    font-size: 40px;
}

.main-contain {
    margin: 100px 0;
}

.head-title {
    position: relative;
}

.line-left {
    position: absolute;
    left: 0;
    border: solid 1px black;
    width: 37%;
    top: 15px;
}

.line-right {
    position: absolute;
    right: 0;
    border: solid 1px black;
    width: 37%;   
    top: 15px; 
}

.h-title {
    text-align: center;
    opacity: 0.8;
    font-style: oblique;
    font-size: 30px;
}

.stacks-container {
    display: flex;
    padding: 60px 0;
    overflow: hidden;
}

.stacks {
    display: flex;
    margin-right: 13px;
    border: solid 2px black;
    grid-gap: 20px;
    gap: 20px;
    width: 15.5rem;
    height: 4rem;
    align-items: center;
}

img {
    width: 3rem;
    height: 3rem;
    margin-left: 8px;

}

.info {
    display: flex;
    flex-direction: column;
    /* gap: 10px; */
    margin: auto 0;
}

.info-name {
    font-size: 23px;
    opacity: 400;
    text-transform: uppercase;
}
* {
    padding: 0;
    margin: 0;
} 

.line {
    height: 40px;
    display: flex;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    background: #333;
    font-family: "Work Sans";
    text-align: center;
    /* background-color: #145858; */
    line-height: 1;
    text-transform: uppercase;
    border: none;
    border-bottom: 2px solid #E5E4E2;
} 

